import server from '../../server.js';
export default {
	data() {
		return {
			MsgList:[],
			auid:0,
			val:'',
			value:""
		}
	},
	onLoad:function(e){

		this.getMyMsg()
		
	},
	onShow:function(){
		this.markmsg();
	},
	methods: {
		input:function(e){
			this.val=e.target.value 
		},
		getme:function(){
			uni.navigateTo({
				url: '../i/i'
			})
		},
		markmsg:function(){
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/user.php?type=mark&class=system", //请求标记已读消息
				data: {
					'msgId':this.msgid
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					console.log(res)
					if(res.data.code=="1"){
						console.log('已将信息标记为已读')
					}else{
						console.log('标记已读失败！')
					}
					
				},
				complete() {
					
				}
			});
		},
		InputFocus(e) {
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			this.InputBottom = 0
		},
		getMyMsg:function(){
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/user.php?type=getSystemMsg", //仅为示例，并非真实接口地址。
				data: {
					
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					console.log(res)
					if(res.data.code=="1"){
						this.MsgList=res.data.data
					}
					console.log(this.MsgList)
				}
			});
		},
	}
}
